@use "variables" as *;
/*******************************************************************************
 * Styles for the base elements of the theme.
 ******************************************************************************/

// Typography

p, h1, h2, h3, h4, h5, h6, em, div, li, span, strong {
 color: var(--global-text-color);
}

// Default readable text sizing
p {
 font-size: 1.083rem; // ~1pt larger than 16px base (down from 1.167rem)
 line-height: 1.5;
}

// Apply same readable sizing to list items
li {
 font-size: 1.083rem;
 line-height: 1.5;
}

// Personal section uses the same body typography even without <p> wrappers
#personal {
 font-size: 1.083rem;
 line-height: 1.5;
}

// News list slightly smaller than body
.news {
 table {
 td { font-size: 1.083rem; line-height: 1.5; }
 th { font-size: 1rem !important; }
 }
}

.custom-content {
 font-family: "Gill Sans";
 font-size: 0.92rem;
 line-height: 1.42;
}

.custom-content h2 {
 font-size: 2rem;
 font-weight: 500;
 margin-top: 0.5rem;
 margin-bottom: 0.25rem;
}

hr {
 border-top: 1px solid var(--global-divider-color);
}

table {
 td, th {
 color: var(--global-text-color);
 }
 td {
 font-size: 1rem;
 }
}

a, table.table a {
 color: var(--global-theme-color);
 text-decoration-thickness: 0.08em;
 text-underline-offset: 0.15em;
 &:hover {
 color: var(--global-theme-color);
 text-decoration: underline;
 }
 &:focus-visible {
 outline: 2px dashed var(--global-theme-color);
 outline-offset: 2px;
 text-decoration: none;
 }
 &:hover:after :not(.nav-item.dropdown) {
 width: 100%;
 }
}

figure, img {
 max-width: 90vw;
}

blockquote {
 background: var(--global-bg-color);
 border-left: 2px solid var(--global-theme-color);
 margin: 1.5em 10px;
 padding: 0.5em 10px;
 font-size: 1.2rem;
}

// Math

.equation {
 margin-bottom: 1rem;
 text-align: center;
}

// Caption

.caption {
 font-size: 0.875rem;
 margin-top: 0.75rem;
 margin-bottom: 1.5rem;
 text-align: center;
}

// Card

.card {
 background-color: var(--global-card-bg-color);

 img {
 width: 100%;
 }

 .card-title {
 color: var(--global-text-color);
 }

 .card-item {
 width: auto;
 margin-bottom: 10px;

 .row {
 display: flex;
 align-items: center;
 }
 }
}

// Citation

.citation, .citation-number {
 color: var(--global-theme-color);
}

// Profile


.address {
 font-family: monospace;
}


@media (min-width: 576px) {
 .profile {
 width: 30%;
 .address {
 p { display: block; }
 }
 }
}



// Footer
footer.fixed-bottom {
 background-color: var(--global-footer-bg-color);
 font-size: 0.75rem;
 .container {
 color: var(--global-footer-text-color);
 padding-top: 9px;
 padding-bottom: 8px;
 }
 a {
 color: var(--global-footer-link-color);
 &:hover {
 color: var(--global-theme-color);
 text-decoration: none;
 }
 }
}

// Blog

.header-bar {
 border-bottom: 1px solid var(--global-divider-color);
 text-align: center;
 padding-top: 2rem;
 padding-bottom: 3rem;
 h1 {
 color: var(--global-theme-color);
 font-size: 5rem;
 }
}

.tag-list {
 border-bottom: 1px solid var(--global-divider-color);
 text-align: center;
 padding-top: 1rem;

 ul {
 justify-content: center;
 display: flow-root;

 p, li {
 list-style: none;
 display: inline-block;
 padding: 1rem 0.5rem;
 color: var(--global-text-color-light);
 }
 }
}

.post-list {
 margin: 0;
 margin-bottom: 40px;
 padding: 0;
 li {
 border-bottom: 1px solid var(--global-divider-color);
 list-style: none;
 padding-top: 2rem;
 padding-bottom: 2rem;
 .post-meta {
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 margin-bottom: 0;
 }
 .post-tags {
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 padding-top: 0.25rem;
 padding-bottom: 0;
 }
 a {
 color: var(--global-text-color);
 text-decoration: none;
 &:hover {
 color: var(--global-theme-color);
 }
 }
 }
}

.pagination {
 .page-item {
 .page-link {
 color: var(--global-text-color);
 &:hover {
 color: $black-color;
 }
 }
 &.active .page-link {
 color: $white-color;
 background-color: var(--global-theme-color);
 &:hover {
 background-color: var(--global-theme-color);
 }
 }
 }
}


// Distill

.distill {
 a:hover {
 border-bottom-color: var(--global-theme-color);
 text-decoration: none;
 }
}


// Publications

.publications {
 margin-top: 2rem;

 h1 {
 color: var(--global-theme-color);
 font-size: 2rem;
 text-align: center;
 margin-top: 1em;
 margin-bottom: 1em;
 }
 h2 {
 margin-bottom: 1rem;
 span {
 font-size: 1.5rem;
 }
 }
 h4.year {
 color: var(--global-divider-color);
 border-top: 2px solid var(--global-divider-color);
 padding-top: 1rem;
 margin-top: 2rem;
 margin-bottom: -2rem;
 text-align: right;
 }

 .badge {
 color: var(--global-text-color);
 background-color: var(--global-theme-color);
 min-width: 68px;
 }

 ol.bibliography {
 list-style: none;
 padding: 0;
 margin-top: 0;

 li {
 width: 100%;
 margin-bottom: 1rem;
 .preview {
 width: 100%;
 min-width: 80px;
 max-width: 200px;
 }
 .abbr {
 height: 2rem;
 margin-bottom: 0.5rem;
 abbr {
 display: inline-block;
 background-color: var(--global-theme-color);
 padding-left: 1rem;
 padding-right: 1rem;
 box-shadow: 0 0 0 0px !important;
 border-radius: 0.1rem;
 padding-left: 5px;
 padding-right: 5px;
 a {
 color: white;
 &:hover {
 text-decoration: none;
 }
 }
 }
 .award {
 color: var(--global-theme-color) !important;
 border: 1px solid var(--global-theme-color);
 }
 }
 .title {
 font-weight: bolder;
 }
 .author {
 a {
 border-bottom: 1px dashed var(--global-theme-color);
 &:hover {
 border-bottom-style: solid;
 text-decoration: none;
 }
 }
 > em {
 border-bottom: 1px solid;
 font-style: normal;
 }
 > span.more-authors {
 color: var(--global-text-color-light);
 border-bottom: 1px dashed var(--global-text-color-light);
 cursor: pointer;
 &:hover {
 color: var(--global-text-color);
 border-bottom: 1px dashed var(--global-text-color);
 }
 }
 }
 .links {
 a.btn {
 color: var(--global-text-color);
 border: 1px solid var(--global-text-color);
 padding-left: 1rem;
 padding-right: 1rem;
 padding-top: 0.25rem;
 padding-bottom: 0.25rem;
 &:hover {
 color: var(--global-theme-color);
 border-color: var(--global-theme-color);
 }
 }
 }
 .hidden {
 font-size: 0.875rem;
 max-height: 0px;
 overflow: hidden;
 text-align: justify;
 transition-property: 0.15s ease;
 -moz-transition: 0.15s ease;
 -ms-transition: 0.15s ease;
 -o-transition: 0.15s ease;
 transition: all 0.15s ease;

 p {
 line-height: 1.4em;
 margin: 10px;
 }
 pre {
 font-size: 1em;
 line-height: 1.4em;
 padding: 10px;
 }
 }
 .hidden.open {
 max-height: 100em;
 transition-property: 0.15s ease;
 -moz-transition: 0.15s ease;
 -ms-transition: 0.15s ease;
 -o-transition: 0.15s ease;
 transition: all 0.15s ease;
 }
 div.abstract.hidden {
 border: dashed 1px var(--global-bg-color);
 }
 div.abstract.hidden.open {
 border-color: var(--global-text-color);
 }
 }
 }
}


code {
 color: var(--global-theme-color);
 background-color: var(--global-code-bg-color);
 border-radius: 3px;
 padding: 3px 3px;
}



// Extra Markdown style (post Customization)
.post{
 .post-meta{
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 margin-bottom: 0;
 }
 .post-tags{
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 padding-top: 0.25rem;
 padding-bottom: 1rem;
 a {
 color: var(--global-text-color-light);
 text-decoration: none;
 &:hover {
 color: var(--global-theme-color);
 }
 }
 }
 .post-content{
 blockquote {
 border-left: 5px solid var(--global-theme-color);
 padding: 8px;
 }
 }
}

article.custom-content h2#personal {
 font-size: 2rem;
 font-weight: 500;
 margin-top: 0.5rem;
 margin-bottom: 0.25rem;
}
article.custom-content h2#personal + hr {
 border-width: 2px;
 margin-top: 0.25rem;
 margin-bottom: 0.75rem;
}
